import React from 'react';

const getDocs = (componentName, knobs, componentThis) => {
  const docs = Object.values(knobs)
    .map(knob =>
      knob
        .reduce((acc, curr) => {
          const value =
            componentThis.state[curr.state] && componentThis.state[curr.state].value
              ? componentThis.state[curr.state].value
              : componentThis.state[curr.state];
          acc.push(`${curr.state} = {"${value || ''}"}`);
          return acc;
        }, [])
        .join('\n\r  '),
    )
    .join();

  return (
    <pre className="tw-docs-code ">
      {`<${componentName}
  ${docs}
/>`}
    </pre>
  );
};

export default getDocs;



